import React from "react";
import Layout from "../../components/shared/Layout/Layout";
import { useSelector } from "react-redux";

const AdminHome = () => {
    const { user } = useSelector((state) => state.auth);
    return (
        <Layout>
            <div className="container">
                <div className="d-felx flex-column mt-4">
                    <h1>
                        Welcome Admin <i className="text-success">{user?.name}</i>
                    </h1>
                    <h3>Manage Blood Bank Application </h3>
                    <hr />
                    <p>
                        The Blood Bank Management System is a comprehensive application built on the MERN stack designed to manage and track blood inventory and transactions between donors, hospitals, and organizations. As an administrator, your role is to oversee the entire system, with an administrative homepage providing key functionalities for system oversight. This includes the ability to view and manage all registered donors, hospitals, and organizations, access a dedicated Analytics dashboard to monitor real-time blood inventory statistics, and maintain data integrity by deleting user records when necessary.
                    </p>
                </div>
            </div>
        </Layout>
    );
};

export default AdminHome;